.header {
  $root: &;

  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  box-sizing: border-box;
  width: 100%;
  padding: 32px 70px;
  display: flex;

  @media (max-width: 1200px) {
    padding-left: 48px;
    padding-right: 48px;
  }

  @media (max-width: 768px) {
    padding: 28px 0;
    flex-direction: column;
  }

  &::before {
    content: "";
    margin-right: 72px;
  }

  &::before,
  &__actions-wrapper {
    width: 100%;
    max-width: 490px;

    @media (max-width: 768px) {
      width: auto;
      max-width: 100%;
    }
  }

  &__actions-wrapper {
    margin-left: 72px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__join-link,
  &__menu-button {
    position: relative;
    font-size: 14px;
    line-height: 1.4em;
    font-weight: 700;
    letter-spacing: -0.015em;
    transition-property: transform, opacity;
    transition-duration: $transition-duration;

    @media (max-width: 768px) {
      position: fixed;
      top: 50%;
      box-sizing: border-box;
      min-height: 114px;
      transform: translateY(-50%) rotate(180deg);
      font-size: 8px;
      text-transform: uppercase;
      font-weight: 400;
      letter-spacing: 0.025em;
      color: #fefefe;
      background-color: #282828;
    }

    &:hover {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.3;
    }
  }

  &__join-link,
  &__button-text {
    @media (max-width: 768px) {
      writing-mode: vertical-lr;
      text-orientation: mixed;
    }
  }

  &__join-link {
    margin-right: 24px;
    padding: 12px 0 12px 72px;
    color: inherit;
    text-decoration: none;

    @media (max-width: 768px) {
      left: 0;
      padding: 12px;
    }

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      width: 65px;
      height: 10px;
      transform: translateY(-50%);
      background-image: url("../img/curved-line.png");
      background-size: contain;
      background-repeat: no-repeat;

      @media (max-width: 768px) {
        content: none;
      }
    }

    &--hidden {
      @media (max-width: 768px) {
        transform: translateY(-50%) translateX(100%) rotate(180deg);
      }
    }
  }

  &__button-text {
    opacity: 1;
    transition: opacity 0.15s;
  }

  &__menu-button {
    z-index: 3;
    cursor: pointer;
    border: none;
    padding: 12px 32px 12px 0;
    background-color: transparent;

    @media (max-width: 768px) {
      padding: 12px;
      right: 0;
    }

    &::before,
    &::after {
      content: "";
      position: absolute;
      right: 0;
      transform: translateY(-50%);
      height: 2px;
      background-color: #000000;

      @media (max-width: 768px) {
        content: none;
      }
    }

    &::before {
      top: calc(50% - 3px);
      width: 25px;
      transition: transform $transition-duration;
    }

    &::after {
      top: calc(50% + 3px);
      width: 20px;
      transition-property: width, transform;
      transition-duration: $transition-duration;
    }

    &--opened {
      &::before {
        top: 50%;
        transform: translateY(-50%) rotate(45deg);
      }

      &::after {
        top: 50%;
        width: 25px;
        transform: translateY(-50%) rotate(-45deg);
      }

      #{$root}__button-text {
        opacity: 0;
      }
    }

    &--hidden {
      @media (max-width: 768px) {
        transform: translateY(-50%) translateX(-100%) rotate(180deg);
      }
    }

    &:focus {
      outline: none;
      opacity: 0.7;
    }
  }

  &__instagram-link {
    margin: 0 auto;
    min-width: 53px;
    min-height: 53px;
    max-width: 53px;
    max-height: 53px;
    transition: opacity $transition-duration;
    background-image: url("../img/instagram-icon.svg");
    background-repeat: no-repeat;
    background-size: contain;

    @media (max-width: 768px) {
      min-width: auto;
      min-height: auto;
      width: 26px;
      height: 26px;
    }

    &:hover {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.3;
    }
  }

  &__menu-outer-wrapper {
    position: fixed;
    right: 0;
    top: 0;
    width: 82%;
    height: 100vh;
    transform: translateX(100vw);
    overflow: scroll;
    background-color: #f5b940;
    transition: transform 1s ease-out;

    @media (max-width: 768px) {
      width: 89%;
    }

    &--opened {
      transform: translateX(0);
    }
  }

  &__menu-inner-wrapper {
    height: 100%;
    display: flex;

    @media (max-width: 1200px) {
      flex-direction: column;
    }
  }

  &__nav-wrapper,
  &__contacts-wrapper {
    box-sizing: border-box;
    border-left: 2px solid rgba(#707172, 0.3);
    display: flex;
    flex-direction: column;
    justify-content: center;

    @media (max-width: 1200px) {
      border-width: 1px;
    }
  }

  &__nav-wrapper {
    width: 60%;

    @media (max-width: 1200px) {
      width: 100%;
      border-bottom: 1px solid rgba(#707172, 0.3);
      padding-top: 120px;
      padding-bottom: 80px;
    }

    @media (max-width: 768px) {
      padding: 72px 0;
    }

    @media (max-width: 540px) {
      padding: 48px 0;
    }
  }

  &__contacts-list,
  &__nav-list {
    list-style: none;
  }

  &__contacts-wrapper {
    width: 40%;
    align-items: center;

    @media (max-width: 1200px) {
      width: 100%;
      align-items: flex-start;
      padding: 48px 64px;
    }

    @media (max-width: 540px) {
      padding: 20px 32px;
    }
  }

  &__nav-list {
    counter-reset: item;
    padding: 0 102px;
    font-size: 28px;
    line-height: 1.25em;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    @media (max-width: 1200px) {
      padding: 0 64px;
    }

    @media (max-width: 768px) {
      padding: 0 48px;
      font-size: 18px;
    }

    @media (max-width: 540px) {
      padding: 0 32px;
      font-size: 16px;
    }
  }

  &__nav-item {
    position: relative;
    padding-left: 66px;
    margin-bottom: 66px;
    transition: opacity $transition-duration;

    @media (max-width: 768px) {
      margin-bottom: 48px;
    }

    @media (max-width: 540px) {
      padding-left: 32px;
      margin-bottom: 32px;
    }

    &:last-child {
      margin-bottom: 0;
    }

    &::before {
      counter-increment: item;
      content: counter(item, decimal-leading-zero) ".";
      position: absolute;
      top: 0;
      left: 0;
    }

    &--faded {
      opacity: 0.3;
    }

    &--active {
      opacity: 1;
      #{$root}__nav-link {
        transform: translateX(36px);
      }
    }
  }

  &__nav-link {
    display: block;
    color: inherit;
    text-decoration: none;
    transition: transform $transition-duration linear;
  }

  &__contacts-list {
    font-size: 0;
  }

  &__contacts-item {
    display: inline-block;
    margin-right: 34px;

    @media (max-width: 540px) {
      margin-right: 16px;
    }

    &--separate {
      display: block;
      margin-right: 0;
      margin-bottom: 34px;

      @media (max-width: 540px) {
        margin-bottom: 16px;
      }
    }
  }

  &__text-contact {
    display: inline-block;
    position: relative;
    padding-bottom: 8px;
    font-size: 20px;
    line-height: 1.3em;
    color: #282828;
    letter-spacing: 0.04em;
    text-decoration: none;
    text-transform: uppercase;
    opacity: 0.6;
    transition: opacity $transition-duration;

    @media (max-width: 1200px) {
      opacity: 1;
    }

    @media (max-width: 768px) {
      padding-bottom: 6px;
      font-size: 16px;
    }

    @media (max-width: 540px) {
      padding-bottom: 4px;
      font-size: 10px;
    }

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      transform: translateX(-50%);
      left: 50%;
      width: 100%;
      height: 1px;
      background-color: #282828;
      transition: width $transition-duration ease;

      @media (max-width: 540px) {
        opacity: 0.6;
      }
    }

    &:hover {
      opacity: 1;

      &::after {
        width: 110%;

        @media (max-width: 768px) {
          width: 100%;
        }
      }
    }
  }

  &__icon-contact {
    display: inline-block;
    width: 50px;
    height: 40px;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: left top;
    opacity: 0.6;
    transition: opacity $transition-duration;

    @media (max-width: 1200px) {
      opacity: 1;
    }

    @media (max-width: 540px) {
      width: 28px;
      height: 23px;
    }

    &:hover {
      opacity: 1;
    }

    &--fb {
      background-image: url("../img/facebook-icon.svg");
    }

    &--vk {
      background-image: url("../img/vk-icon.svg");
    }

    &--tg {
      background-image: url("../img/telegram-icon.svg");
    }
  }

  &__mobile-close-button {
    display: none;
    position: absolute;
    bottom: 48px;
    right: 48px;
    width: 40px;
    height: 40px;
    padding: 0;
    border: none;
    background-color: transparent;
    background-image: url("../img/close-icon.svg");

    @media (max-width: 768px) {
      display: block;
    }

    @media (max-width: 540px) {
      bottom: 32px;
      right: 32px;
    }
  }
}
