.join-page {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;
  background-image: url("../img/form-bg-desktop@1x.png");

  @media (max-width: 768px) {
    background-image: url("../img/form-bg-tablet@1x.png");
  }

  @media (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
    background-image: url("../img/form-bg-desktop@2x.png");

    @media (max-width: 768px) {
      background-image: url("../img/form-bg-tablet@2x.png") !important;
    }
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 0;
    background-image: url("../img/thanks-bg-desktop@1x.png");
    opacity: 0;
    transition: opacity 1s;

    @media (max-width: 768px) {
      background-image: url("../img/thanks-bg-tablet@1x.png");
    }

    @media (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
      background-image: url("../img/thanks-bg-desktop@2x.png");

      @media (max-width: 768px) {
        background-image: url("../img/thanks-bg-tablet@2x.png") !important;
      }
    }
  }

  &--thanks-step {
    &::after {
      opacity: 1;
    }
  }

  &__form {
    position: relative;
    z-index: 2;

    &--hidden {
      display: none;
    }
  }

  &__inputs-container {
    position: relative;
    top: 49vh;
    left: 14vw;

    @media (max-width: 768px) {
      left: 12.5vw;
    }

    @media (max-width: 768px) {
      top: 47vh;
    }
  }

  &__input-wrapper {
    position: relative;
    max-width: 50vw;
    padding-top: 86px;
    font-size: 50px;
    line-height: 1em;
    text-transform: uppercase;
    letter-spacing: 0.04em;
    transition-property: opacity, transform;
    transition-duration: $transition-duration, 1s;

    @media (max-width: 1200px) {
      max-width: 70vw;
    }

    @media (max-width: 768px) {
      padding-top: 80px;
      font-size: 36px;
    }

    @media (max-width: 540px) {
      padding-top: 43px;
      font-size: 20px;
    }

    &--hidden {
      position: absolute;
      z-index: -1;
      left: 0;
      top: 0;
      transform: translateX(40%);
      opacity: 0;
      transition: none;
    }
  }

  &__label {
    position: absolute;
    left: 0;
    bottom: 98px;
    display: block;
    font-weight: 700;

    @media (max-width: 768px) {
      bottom: 92px;
    }

    @media (max-width: 540px) {
      bottom: 55px;
    }

    &--with-long-text {
      font-size: 40px;

      @media (max-width: 768px) {
        font-size: 24px;
      }

      @media (max-width: 540px) {
        font-size: 16px;
      }
    }
  }

  &__field {
    padding: 0;
    border: none;
    border-bottom: 1px solid #000000;
    font: inherit;
    background-color: transparent;

    @media (max-width: 540px) {
      width: 204px;
    }

    &:focus {
      outline: none;
    }
  }

  &__next-button {
    cursor: pointer;
    position: relative;
    margin-top: 11vh;
    left: 180px;
    padding: 0;
    border: none;
    width: 200px;
    height: 180px;
    background-color: transparent;

    @media (max-width: 768px) {
      margin-top: 8vh;
      left: 80px;
      width: 180px;
      height: 140px;
    }

    @media (max-width: 540px) {
      margin-top: 11vh;
      width: 110px;
      height: 90px;
    }

    &::before {
      content: "";
      position: absolute;
      z-index: 3;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-repeat: no-repeat;
      background-size: 40px auto;
      background-position: center center;
      background-image: url("../img/arrow-icon@1x.png");

      @media (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
        background-image: url("../img/arrow-icon@2x.png");
      }

      @media (max-width: 540px) {
        background-size: 28px auto;
      }
    }

    &:focus {
      outline: none;
    }
  }

  &__button-wave-face {
    position: relative;
    z-index: 2;

    // path {
    //   animation: shake-button 1s infinite;
    // }
  }

  &__button-wave-shadow {
    position: absolute;
    opacity: 0.54;
    top: 24px;
    left: 8px;
  }

  &__thanks-container {
    position: relative;
    top: 36vh;
    opacity: 0;
    transition: opacity 1s;
    text-align: center;

    &--visible {
      z-index: 2;
      opacity: 1;
    }
  }

  &__thanks-text {
    display: grid;
    margin: 0;
  }

  &__thanks-top-word {
    margin-bottom: 28px;
    font-size: 64px;
    line-height: 1em;
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 0.1em;

    @media (max-width: 768px) {
      margin-bottom: 12px;
      font-size: 48px;
    }

    @media (max-width: 540px) {
      font-size: 26px;
    }
  }

  &__thanks-bottom-words {
    max-width: 700px;
    margin: 0 auto;
    font-size: 38px;
    line-height: 1.2em;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.04em;

    @media (max-width: 768px) {
      font-size: 20px;
      max-width: 340px;
    }

    @media (max-width: 540px) {
      max-width: 260px;
      font-size: 16px;
    }
  }

  &__social-wrapper {
    margin-top: 11vh;
    display: grid;
    justify-items: center;

    @media (max-width: 768px) {
      margin-top: 6vh;
    }
  }

  &__social-text {
    margin: 0;
    max-width: 480px;
    font-size: 28px;
    line-height: 1.3em;
    font-weight: 600;
    text-transform: uppercase;

    @media (max-width: 768px) {
      max-width: 220px;
      font-size: 13px;
    }
  }

  &__social-list {
    margin: 0;
    margin-top: 4vh;
    padding: 0;
    list-style: none;
    display: flex;
  }

  &__social-item {
    margin-right: 70px;

    @media (max-width: 768px) {
      margin-right: 18px;
    }

    &:last-child {
      margin-right: 0;
    }
  }

  &__social-icon {
    height: auto;

    @media (max-width: 768px) {
      width: 80px;
    }
  }

  &__back-link {
    display: none;
    align-items: center;
    justify-content: center;
    margin-top: 24px;
    width: 100px;
    height: 60px;
    font-size: 8px;
    line-height: 1em;
    font-weight: 600;
    letter-spacing: 0.025em;
    text-transform: uppercase;
    color: #ffffff;
    text-decoration: none;
    background-size: contain;
    background-image: url("../img/back-button-bg.svg");

    @media (max-width: 767px) {
      display: flex;
    }
  }

  &__confetti-wrapper {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

html.webp .join-form {
  background-image: url("../img/form-bg-desktop@1x.webp");

  @media (max-width: 768px) {
    background-image: url("../img/form-bg-tablet@1x.webp");
  }

  @media (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
    background-image: url("../img/form-bg-desktop@2x.webp");

    @media (max-width: 768px) {
      background-image: url("../img/form-bg-tablet@1x.webp") !important;
    }
  }

  &::after {
    background-image: url("../img/thanks-bg-desktop@1x.webp");

    @media (max-width: 768px) {
      background-image: url("../img/thanks-bg-tablet@1x.webp");
    }

    @media (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
      background-image: url("../img/thanks-bg-desktop@2x.webp");

      @media (max-width: 768px) {
        background-image: url("../img/thanks-bg-tablet@1x.webp") !important;
      }
    }
  }

  &__next-button::before {
    background-image: url("../img/arrow-icon@1x.webp");

    @media (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
      background-image: url("../img/arrow-icon@2x.webp");
    }
  }
}

